<template>
  <div class="course-construction">
    <title-curmb></title-curmb>
    <course-steps :title="courseName" :step="2"></course-steps>
    <div class="course-construction_body">
      <el-form :model="form" :rules="rules" ref="form" label-width="0">
        <h2>课程目标</h2>
        <el-row>
          <el-form-item prop="goals">
            <content-item
              v-for="(vo, index) in goals"
              :key="index"
              :data="vo"
              :indexVal="index"
              @onChange="contentItemChange"
              @onDelete="contentItemDel"
            ></content-item>
          </el-form-item>
          <div @click="addgoals" class="add-step-title add-step-button">
            <SvgIcon icon-class="icontianjia" size="20px"></SvgIcon>
            <span>添加课程目标</span>
          </div>
        </el-row>
        <h2>课程目标与毕业要求指标点的对应关系</h2>
        <el-row>
          <el-form-item prop="points">
            <el-table :data="points"  border class="cc-table" style="width: 100%;">
              <el-table-column label="毕业要求">
                <template slot-scope="scope">
                  <div v-if="scope.row.changed ===0">
                    <el-select @change="claimChange(scope.row.claimId, scope.$index)" style="width: 80%" v-model="scope.row.claimId" placeholder="请选择毕业要求">
                      <el-option
                        v-for="item in claimOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                      </el-option>
                    </el-select>
                  </div>
                  <div v-else>{{scope.row.claimName}}</div>
                </template>
              </el-table-column>
              <el-table-column label="毕业要求指标点">
                <template slot-scope="scope">
                  <div v-if="scope.row.changed === 0">
                    <el-select style="width: 80%" v-model="scope.row.pointId" placeholder="请选择毕业要求指标点">
                      <el-option
                        v-for="item in scope.row.pointOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                      </el-option>
                    </el-select>
                  </div>
                  <div v-else>{{scope.row.pointContent}}</div>
                </template>
              </el-table-column>
              <el-table-column label="课程目标">
                <template slot-scope="scope" style="position:relative">     
                  <el-select
                    v-model="scope.row.goalChecked"
                    value-key="id"
                    filterable
                    multiple
                    placeholder="请选择"
                    class="my-input my-input_mg"
                  >
                    <el-option
                      v-for="item in goals"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                  <i v-if="scope.row.changed ===0" @click="deleteOptions(scope.$index)" style="font-size: 21px;position: absolute; right: 5px; top: 50%; transform: translateY(-50%); cursor: pointer; color: #aaa;z-index:10" class="el-icon-remove-outline"></i>
                </template>
              </el-table-column>
            </el-table>
            <div @click="addObjective" class="add-step-title add-step-button">
              <SvgIcon icon-class="icontianjia" size="20px"></SvgIcon>
              <span>添加课程目标与毕业要求关系</span>
            </div>
          </el-form-item>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="6" :offset="9">
            <div class="c-footer">
              <el-button @click="formSubmit('form', 'save')">保&nbsp;&nbsp;存</el-button>
              <el-button type="primary" @click="formSubmit('form', 'next')">下一步</el-button>
            </div>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </div>
</template>

<script>
import TitleCurmb from "./components/TitleCurmb";
import CourseSteps from "./components/CourseSteps";
import ContentItem from "./components/ContentItem";

export default {
  name: "TeachingGoal",
  components: { TitleCurmb, CourseSteps, ContentItem },
  data() {
    const checkgoals = (rule, value, callback) => {
      let num = 0;
      if (this.goals.length === 0) {
        callback(new Error("请添加课程目标"));
      }
      this.goals.forEach(vo => {
        if (vo.name === "" || vo.content === "") {
          num++;
        }
      });
      if (num === 0) {
        callback();
      } else {
        callback(new Error("课程目标内容不能有空"));
      }
    };
    const checkpoints = (rule, value, callback) => {
      let n = 0;
      if (this.points.length === 0) {
        callback(new Error("请添加课程目标与毕业要求指标点的对应关系"));
      }
      this.points.forEach(vo => {
        if (vo.goalChecked.length === 0 || vo.claimId === '' || vo.pointId === '') {
          n++;
        }
      });
      if (n === 0) {
        callback();
      } else {
        callback(new Error("课程目标与毕业要求指标点的对应关系内容不能有空"));
      }
    };
    return {
      claimOptions: [],
      courseName: this.$route.query.title,
      goals: [],
      goalsCopy:[],
      points: [],
      form: {
        goals: [],
        points: []
      },
      rules: {
        goals: [
          { required: true, validator: checkgoals, trigger: "blur" }
        ],
        points: [
          { required: true, validator: checkpoints, trigger: "blur" }
        ]
      },
      api:{
         details:courseId=>`/basic/course/setTargetView/${courseId}`, //详情
         graduate:versionId=>`/basic/claim/listByVersionId/${versionId}`, //毕业要求
         getId:() =>`/basic/courseGoal/getId`, //获取动态ID
         courseGoalDel:courseGoalId => `basic/courseGoal/del/${courseGoalId}`, //删除课程目标
         claim: claimId => `/basic/point/Claim/${claimId}`, //所有毕业要求
         save:() => `/basic/course/setTarget`
      }
    };
  },
  created() {},
  mounted() {
    this.initData()
    this.getGraduate()
  },
  methods: {
    initData(){
        const url=this.api.details(this.$route.query.id)
        this.$get(url).then(res =>{
            if(res.status ===1){       
               const goalArr=res.data.goals || []
               const pointArr=res.data.points || []
               goalArr.forEach(item =>{
                   //操作标识changed（0新增 1修改 2删除）
                  item.changed=1,
                  item.courseId=this.$route.query.id
               })   
               pointArr.forEach((item, index) =>{
                 //操作标识changed（0新增 1修改 2删除）
                 item.changed=1
                 item.goalChecked=[]
                 item.goal.forEach(vo =>{
                    vo.changed=1
                    item.goalChecked.push(vo.courseGoalId)
                 })
               })
               this.goals=goalArr
               this.goalsCopy=JSON.parse(JSON.stringify(goalArr))
               this.points=pointArr
            }else{
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
            }
        })
     },
    getGraduate(){
        const url=this.api.graduate(localStorage.getItem('versionId'))
        this.$get(url).then(res => {
          if(res.status === 1) {
            this.claimOptions = res.data
          }
      })
    },
    deleteOptions(index){
      this.points.splice(index, 1);
    },
    addObjective() {
      const obj={
        claimName: '',
        claimId:'',
        pointContent: '',
        pointId:'',
        goalChecked: [],
        goal:[],
        changed: 0,
        pointOptions: []
      }
      this.points.push(obj)
    },
    claimChange(val, index) {
      this.$get(this.api.claim(val)).then(res => {
        if(res.status === 1) {
          this.points[index].point = ''
          this.points[index].pointOptions = res.data
        }
      })
    },
    addgoals() {
       this.$get(this.api.getId()).then(res => {
            const obj={
                name: "课程目标",
                content: "",
                changed:0,
                courseId:this.$route.query.id,
                id: res
              }
            this.goals.push(obj)
            this.goalsCopy.push(obj)
        });
     
    },
    contentItemChange(index, val) {
      const changeId= this.goals[index].id
      this.goals[index] = val;
      this.goalsCopy.forEach(item =>{  
        if(item.id === changeId){
            item.name=val.name;
            item.content=val.content;
        }
      })
    },
    contentItemDel(index) {
      const delId= this.goals[index].id
      this.goals.splice(index, 1);
      this.goalsCopy.forEach((item, n) =>{
        if(item.id === delId){
            if(item.changed === 0){
                this.goalsCopy.splice(n, 1);
            }else{
               item.changed=2;
            }     
        }
      })
        //删除表格中已选择的课程目标
      this.points.forEach(vo => {
        vo.goalChecked.forEach((item, n) => {
          if (item === delId) {
              vo.goalChecked.splice(n, 1);
          }
        });
      });

    },
    formSubmit(formName, type) {
      if (type === "save") {
        this.$refs[formName].resetFields();
        this.userSave('save');
      } else {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.userSave('next');
          }
        });
      }
    },
    userSave(type){
      const pointArr=[]
          this.points.forEach(item =>{
            item.goalChecked.forEach(vo =>{
                pointArr.push({
                        indexPointId:item.pointId,
                        courseGoalId:vo
                  })
            })

          })
       const postData={
          courseGoals:this.goalsCopy,
          pointCourseGoals:pointArr
      }

      this.$post(this.api.save(), postData).then( res => {
          if(res.status === 1){
              this.$notify.success({
                  title: 'success',
                  duration:1500,
                  message: res.message
                  })
              if(type === 'next'){
                    this.$router.push({
                      path:'/Construction/basicRequirements',
                      query:{
                          id:this.$route.query.id,
                          title:this.courseName
                      }
                  })
              }else{
                  this.initData()
              } 
          }else{
              this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.message
              })
          }  
      })
     },
     //去重
     unique(arr) {
      var h = {};
      var temp = [];
      for (var i = 0; i < arr.length; i++) {
          if (!h[arr[i].id]) {
              h[arr[i].id] = true;
              temp.push(arr[i]);
          }
      }
  
      return temp;
    },
    //随机数
    rndNum(n) {
      let rnd = "";
      for (let i = 0; i < n; i++) {
        rnd += Math.floor(Math.random() * 10);
      }
      return rnd;
    }
  }
};
</script>

<style lang='scss' scoped>
@import "../../styles/courseConstruction.scss";
.my-input_mg{
  margin-right:20px;
}
</style>



