<template>
 <div class="content-item">
    <el-input v-model="data.name" @change="inputChange" placeholder="" class="content-item_disable"></el-input>
    <el-input v-model="data.content" @change="inputChange" placeholder="" class="content-item_input"></el-input>
    <label  @click.stop="delItem" ><SvgIcon icon-class="iconqudiao" size="20px"></SvgIcon></label>
 </div>
</template>

<script>
export default {
 name: 'ContentItem',
 props: {
   data:{
       type:Object,
       default(){
           return {}
       }
   },
   indexVal:{
       type:Number,
       default:0
   }
 },
 data() {
 return {
   
 }
 },
 created(){},
 mounted(){},
 methods:{
    inputChange(){
        this.$emit('onChange',  this.indexVal, this.data)
    },
    delItem(){
       this.$emit('onDelete', this.indexVal)
    }
 }
}
</script>

<style lang='scss' scoped>
.content-item{
    height:40px;
    margin-bottom:10px;
  .content-item_disable{
      width:110px;
      margin-right:10px;
      float:left;
  }
  .content-item_input{
      width:calc(100% - 160px);
      float:left;
      margin-right:7px;
  }
  label{
    display: inline-block;
    line-height: 0;
  }
  .svg-icon{
    color:#969696;
    margin-top:10px;
    cursor:pointer;
  }
}
</style>



